.signup-div {
  display: flex;
  justify-content: center;
  width: 75%;
}
.rightsignup-section {
  width: 50%;
  background-color: #ffffff;
}
.leftsignup-section {
  width: 40%;
  background-color: #3dbb94;
}
.rightside-signupheader,
.rightside-signinheader {
  padding: 20px 20px;
  color: #3dbb94;
}
.leftsidetext-sighinheader,
.rightside-header {
  color: white;
  padding: 20px 20px;
}

.input-username,
.input-email,
.input-password {
  border: none;
  width: 250px;
  background-color: #f5f5f5;
  border-radius: 3rem;
  padding: 8px;
  text-align: center;
}

.signup-button {
  text-decoration: none;
  border: 1px solid #ffffff;
  border-radius: 4rem;
  background-color: #3dbb94;
  padding: 8px 30px;
  color: #ffffff;
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.signup-button:hover {
  background-color: #ffffff;
  color: #3dbb94;
  border: 1px solid #3dbb94;
}

.leftside-header {
  padding: 20px;

  color: #3dbb94;
}
.fas {
  color: #333333;
}

