}
.navbar-item {
  text-decoration: none;
}

.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  background-color: #eceff1;
}
.buttons {
  font-size: 1.3rem;
}

.navbar__link {
  font-size: 1.5rem;
  letter-spacing: 0.3em;
  font-size: 1rem;
  font-weight: 600;
  font-family: "Roboto", sans-serif;
  cursor: pointer;
  text-decoration: none;
  text-indent: 0.3em;
  color: white;
}
.navbar_link {
  margin: 1rem;
  letter-spacing: 0.2em;
  font-size: 1rem;
  text-indent: 0.3em;
  font-weight: 600;
  color: black;
  text-decoration: none;
}

.navbar__link:hover {
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
  color: #3eb88c;
}

.navbar__link--active {
  border-bottom: 3px solid #3eb88c;
  -webkit-transition: border-bottom 0.5s ease-in-out;
  transition: border-bottom 0.5s ease-in-out;
}

.button-header {
  outline: none;
  color: white;
  background-color: #3eb88c;
  padding: 5px 5px;
